<template>
    <el-form :inline="true" :model="searchForm" ref="searchForm" :rules="searchRule" label-width="60px" size="mini" >
        <el-form-item label="线路" prop="lineUuid" width="100%">
            <el-select v-model="searchForm.lineUuid" multiple collapse-tags  placeholder="请选择一条或者多条" width="100%">
                <el-option v-for="line in lines" :key="line.lineUuid" :label="line.lineName" :value="line.lineUuid"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="车辆" prop="busPlateNum">
            <el-select v-model="searchForm.busPlateNum" multiple collapse-tags  placeholder="请选择一条或者多条">
                <el-option v-for="bus in buses" :key="bus.busUuid" :label="bus.busPlateNumber" :value="bus.busPlateNumber"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item style="margin-left:20px;" prop="deviceOnlineStatus">
            <el-radio-group v-model="searchForm.deviceOnlineStatus">
                <el-radio label="">全部</el-radio>
                <el-radio :label="1">在线</el-radio>
                <el-radio :label="0">离线</el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item style="margin-left:20px;">
            <el-button type="primary" size="mini" @click="search">更新显示</el-button>
        </el-form-item>
    </el-form>
</template>
<script>
// api
import { linesList, busesList } from '@/api/common'
export default {
  name: 'DeviceStateSearch',
  data () {
    return {
      lines: [],
      buses: [],
      searchForm: {
        lineUuid: [],
        busPlateNum: [],
        deviceOnlineStatus: 1
      },
      searchRule: {}
    }
  },
  mounted () {
    // 初始化后先根据默认条件查询数据
    linesList({}).then(res => {
      this.lines = res
    })
    busesList({}).then(res => {
      this.buses = res
    })
    this.search()
  },
  methods: {
    // 转换查询参数为后端需要的参数格式
    convertSearchParams () {
      const searchParams = {
        lineUuid: this.searchForm.lineUuid,
        busPlateNum: this.searchForm.busPlateNum,
        deviceOnlineStatus: this.searchForm.deviceOnlineStatus
      }
      return searchParams
    },
    search () {
      this.$emit('search', this.convertSearchParams())
    }
  }
}
</script>



